
p {
    font-size: 18px;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

ul {list-style-type: none;}

body{
    margin: 0px;
}

.App{
    margin: 0px;
    background-color: rgba(0, 33, 71, 0.4); 
}

.projects{
    display: flex;
    height: 350px;
    width: 90%;
    margin: auto;
    background-color: rgba(2, 161, 177, 0.3); 
    border-radius: 40px;
    overflow: hidden;
}

.projectInfo{
    margin-left: 5%;
    margin-right: 5%;
    /* background-color: rgba(14, 152, 227, 0.319); */
    border-radius: 40px;
    color: rgba(255, 255, 255, 1);
}

.projectItems {
    width: 45%;
}

.projectItems ul{
    list-style: none;
    display: flex;
}

.projectItems li {
   margin-right: 5%;
   width: 33%;
   margin-top: 2%;
   /* height: 90%; */
   border: solid rgba(254, 255, 255, 0.175);
   border-radius: 30px;
   text-align: justify;
   text-indent: 0%;
   overflow: hidden;
   margin-bottom: 2%;
}

.projectItems ul li p{
 font-size: 14px;
}

.projectItems ul li p img{
    width: 10%;
    aspect-ratio: 1/1;
}

.projectItems ul li p img:hover{
    cursor:pointer;
}

.projectItem{
    display: flex;
    flex-direction: column;
}